<template>
  <div class="profile-info-form">
    <div class="form-group">
      <label for="bio" class="form-label">Bio</label>
      <textarea 
        id="bio" 
        v-model="bio" 
        class="form-textarea" 
        placeholder="Tell us about yourself (max 500 characters)"
        rows="4"
        maxlength="500"
        autocomplete="off"
      ></textarea>
    </div>
    
    <div class="form-group">
      <label for="telegram" class="form-label">Telegram</label>
      <input 
        type="text" 
        id="telegram" 
        v-model="telegram" 
        class="form-input" 
        placeholder="Your Telegram username (without @)"
        autocomplete="off"
      />
    </div>
    
    <div class="form-group">
      <label for="twitter" class="form-label">Twitter</label>
      <input 
        type="text" 
        id="twitter" 
        v-model="twitter" 
        class="form-input" 
        placeholder="Your Twitter username (without @)"
        autocomplete="off"
      />
    </div>
    
    <div class="form-actions">
      <button 
        class="btn-cancel" 
        @click="$emit('close')" 
        :disabled="loading"
      >
        Cancel
      </button>
      <button 
        class="btn-save" 
        @click="saveProfileInfo" 
        :disabled="loading"
      >
        <span v-if="loading" class="loading-spinner"></span>
        <span v-else>Save Information</span>
      </button>
    </div>
    
    <div v-if="error" class="error-message">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import axios from 'axios';

export default {
  name: 'ProfileInfoForm',
  props: {
    userTag: {
      type: String,
      required: true
    },
    userSettings: {
      type: Object,
      required: true
    }
  },
  emits: ['updated', 'close'],
  setup(props, { emit }) {
    const bio = ref(props.userSettings.bio || '');
    const telegram = ref(props.userSettings.telegram || '');
    const twitter = ref(props.userSettings.twitter || '');
    const loading = ref(false);
    const error = ref('');
    
    const saveProfileInfo = async () => {
      loading.value = true;
      error.value = '';
      
      try {
        const response = await axios.put(
          `/user-settings/${props.userTag}/bio`,
          { 
            bio: bio.value,
            telegram: telegram.value,
            twitter: twitter.value
          },
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          }
        );
        
        if (response.data && response.data.success) {
          emit('updated', { 
            bio: bio.value,
            telegram: telegram.value,
            twitter: twitter.value
          });
        } else {
          error.value = 'Failed to update profile information';
        }
      } catch (err) {
        console.error('Error updating profile info:', err);
        error.value = err.response?.data?.message || 'Failed to update profile information';
      } finally {
        loading.value = false;
      }
    };
    
    return {
      bio,
      telegram,
      twitter,
      loading,
      error,
      saveProfileInfo
    };
  }
};
</script>

