<template>
  <div class="page-user-settings">
    <h2 class="page-title">User Settings</h2>
    
    <div v-if="!isAuthenticated" class="auth-message">
      <p>Please login to access your settings.</p>
      <router-link to="/my" class="login-link">Go to Login Page</router-link>
    </div>
    
    <div v-else class="settings-container">
      <!-- Avatar Section -->
      <div class="settings-section">
        <h3 class="section-title">Profile Avatar</h3>
        <div class="section-content">
          <div class="current-avatar">
            <img 
              :src="userSettings.avatar || bitcatImage" 
              alt="User Avatar" 
              class="avatar-image" 
            />
          </div>
          <button class="action-button" @click="openAvatarModal">Change Avatar</button>
        </div>
      </div>
      
      <!-- Profile Info Section -->
      <div class="settings-section">
        <h3 class="section-title">Profile Information</h3>
        <div class="section-content">
          <div class="info-item">
            <span class="info-label">Bio:</span>
            <span class="info-value">{{ userSettings.bio || 'Not set' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">Telegram:</span>
            <span class="info-value">{{ userSettings.telegram || 'Not set' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">Twitter:</span>
            <span class="info-value">{{ userSettings.twitter || 'Not set' }}</span>
          </div>
          <button class="action-button" @click="openProfileInfoModal">Edit Profile Info</button>
        </div>
      </div>
      
      <!-- Password Section -->
      <div v-if="false" class="settings-section">
        <h3 class="section-title">Password Settings</h3>
        <div class="section-content">
          <div class="info-item">
            <span class="info-label">Password Status:</span>
            <span class="info-value">{{ hasPassword ? 'Password set' : 'No password set' }}</span>
          </div>
          <button class="action-button" @click="openPasswordModal">
            {{ hasPassword ? 'Change Password' : 'Set Password' }}
          </button>
        </div>
      </div>

      <!-- VIP Section -->
      <div class="settings-section">
        <h3 class="section-title">VIP Status<small>(Coming Soon)</small></h3>
        <div class="section-content">
          <div class="info-item">
            <span class="info-label">VIP Status:</span>
            <span class="info-value">{{ userSettings.isVip ? 'Yes' : 'No' }}</span>
            <button class="action-button" disabled>
              Get VIP (for BITCAT)
            </button>
            <br>
            <small>VIP status can be purchased once. It will distinguish you from other users and give certain advantages. In development, stay tuned for updates</small>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Modal Component -->
    <modal-popup 
      :show="showModal" 
      :title="modalTitle" 
      @close="closeModal"
    >
      <component 
        :is="currentModalComponent"
        :userTag="userTag"
        :userSettings="userSettings"
        :hasPassword="hasPassword"
        @updated="handleSettingsUpdated"
        @close="closeModal"
      />
    </modal-popup>
    
    <AppToast 
      :show="toast.show" 
      :message="toast.message" 
      :type="toast.type" 
      @close="toast.show = false" 
      :duration="toast.duration"
    />
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted } from 'vue';
import store from '@/vuex';
import axios from 'axios';
import ModalPopup from '@/components/pages/settings/ModalPopup.vue';
import AvatarForm from '@/components/pages/settings/AvatarForm.vue';
import ProfileInfoForm from '@/components/pages/settings/ProfileInfoForm.vue';
import PasswordForm from '@/components/pages/settings/PasswordForm.vue';
import AppToast from '@/components/Toast.vue';
import bitcatImage from '@/assets/bitcat.webp'

export default {
  name: 'UserSettingsPage',
  components: {
    ModalPopup,
    AvatarForm,
    ProfileInfoForm,
    PasswordForm,
    AppToast
  },
  setup() {
    const userTag = computed(() => store.getters.userTag);
    const isAuthenticated = computed(() => store.getters.isAuthenticated);
    const hasPassword = ref(false);
    
    const userSettings = reactive({
      avatar: null,
      bio: null,
      telegram: null,
      twitter: null,
      isVip: false
    });
    
    const toast = reactive({
      show: false,
      message: '',
      type: 'info',
      duration: 5000
    });
    
    const showModal = ref(false);
    const modalTitle = ref('');
    const currentModalComponent = ref(null);
    
    const fetchUserSettings = async () => {
      if (!isAuthenticated.value) return;
      
      try {
        const response = await axios.get(`/user-settings/${userTag.value}`);
        if (response.data && response.data.success) {
          const settings = response.data.settings;
          userSettings.avatar = settings.avatar;
          userSettings.bio = settings.bio;
          userSettings.telegram = settings.telegram;
          userSettings.twitter = settings.twitter;
          userSettings.isVip = settings.isVip;
        }
        
        // Проверяем, установлен ли пароль
        const userResponse = await axios.get('/auth/me');
        if (userResponse.data && userResponse.data.exists) {
          hasPassword.value = userResponse.data.hasPassword;
        }
      } catch (error) {
        console.error('Error fetching user settings:', error);
        toast.type = 'error';
        toast.message = 'Failed to load user settings';
        toast.show = true;
      }
    };
    
    const openAvatarModal = () => {
      modalTitle.value = 'Change Avatar';
      currentModalComponent.value = 'AvatarForm';
      showModal.value = true;
    };
    
    const openProfileInfoModal = () => {
      modalTitle.value = 'Edit Profile Information';
      currentModalComponent.value = 'ProfileInfoForm';
      showModal.value = true;
    };
    
    const openPasswordModal = () => {
      modalTitle.value = hasPassword.value ? 'Change Password' : 'Set Password';
      currentModalComponent.value = 'PasswordForm';
      showModal.value = true;
    };
    
    const closeModal = () => {
      showModal.value = false;
    };
    
    const handleSettingsUpdated = (updatedSettings) => {
      if (updatedSettings.avatar !== undefined) {
        userSettings.avatar = updatedSettings.avatar;
      }
      
      if (updatedSettings.bio !== undefined) {
        userSettings.bio = updatedSettings.bio;
      }
      
      if (updatedSettings.telegram !== undefined) {
        userSettings.telegram = updatedSettings.telegram;
      }
      
      if (updatedSettings.twitter !== undefined) {
        userSettings.twitter = updatedSettings.twitter;
      }
      
      if (updatedSettings.passwordChanged) {
        hasPassword.value = true;
      }
      
      toast.type = 'success';
      toast.message = 'Settings updated successfully';
      toast.show = true;
      
      closeModal();
    };
    
    onMounted(() => {
      fetchUserSettings();
    });
    
    return {
      userTag,
      isAuthenticated,
      userSettings,
      hasPassword,
      showModal,
      modalTitle,
      currentModalComponent,
      toast,
      openAvatarModal,
      openProfileInfoModal,
      openPasswordModal,
      closeModal,
      handleSettingsUpdated,
      bitcatImage
    };
  }
};
</script>

