<template>
  <div class="avatar-form">
    <div class="avatar-preview">
      <img 
        :src="previewUrl || userSettings.avatar || bitcatImage" 
        alt="Avatar Preview" 
        class="avatar-image" 
      />
    </div>
    
    <div class="form-tabs">
      <!-- <button 
        :class="['tab-button', { active: activeTab === 'url' }]" 
        @click="activeTab = 'url'"
      >
        Custom URL
      </button> -->
      <button 
        :class="['tab-button', { active: activeTab === 'nft' }]" 
        @click="activeTab = 'nft'"
      >
        My NFTs
      </button>
    </div>
    
    <div v-if="activeTab === 'nft'" class="nft-selection">
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner large"></div>
        <p>Loading your NFTs...</p>
      </div>
      
      <div v-else-if="!hasNfts" class="no-nfts-message">
        <p>You don't have any NFTs to use as avatar.</p>
      </div>
      
      <div v-else>
        <div class="nft-tabs">
          <button 
            :class="['nft-tab-button', { active: activeNftTab === 'owned' }]" 
            @click="activeNftTab = 'owned'"
          >
            Owned ({{ nftsData.owned.pagination.total }})
          </button>
          <button 
            :class="['nft-tab-button', { active: activeNftTab === 'rented' }]" 
            @click="activeNftTab = 'rented'"
          >
            Rented ({{ nftsData.rented.pagination.total }})
          </button>
          <button 
            :class="['nft-tab-button', { active: activeNftTab === 'created' }]" 
            @click="activeNftTab = 'created'"
          >
            Created ({{ nftsData.created.pagination.total }})
          </button>
        </div>
        
        <div class="nft-grid">
          <div 
            v-for="(nft, index) in currentNfts" 
            :key="index" 
            :class="['nft-item', { selected: selectedNft && selectedNft.createdAt === nft.createdAt }]"
            @click="selectNft(nft)"
          >
            <div class="nft-image-container">
              <img :src="nft.imageUri" :alt="nft.name" class="nft-image" />
            </div>
            <div class="nft-name">{{ nft.name }}</div>
          </div>
        </div>
        
        <div v-if="currentNfts.length === 0" class="no-nfts-message">
          <p>No NFTs found in this category.</p>
        </div>
        
        <div v-if="showPagination" class="pagination">
          <button 
            class="pagination-button" 
            :disabled="currentPage === 1" 
            @click="changePage(currentPage - 1)"
          >
            Previous
          </button>
          <span class="pagination-info">Page {{ currentPage }} of {{ totalPages }}</span>
          <button 
            class="pagination-button" 
            :disabled="currentPage === totalPages" 
            @click="changePage(currentPage + 1)"
          >
            Next
          </button>
        </div>
      </div>
    </div>

    <div v-else-if="activeTab === 'url' && false" class="form-group">
      <label for="avatar-url" class="form-label">Avatar URL</label>
      <input 
        type="text" 
        id="avatar-url" 
        v-model="avatarUrl" 
        class="form-input" 
        placeholder="Enter URL to your avatar image"
      />
      <p class="form-hint">Enter a direct link to an image (JPG, PNG, GIF)</p>
    </div>
    
    <div class="form-actions">
      <button 
        class="btn-cancel" 
        @click="$emit('close')" 
        :disabled="loading"
      >
        Cancel
      </button>
      <button 
        class="btn-default" 
        @click="setDefaultAvatar" 
        :disabled="loading"
      >
        Set Default
      </button>
      <button 
        class="btn-save" 
        @click="saveAvatar" 
        :disabled="loading || !isValid"
      >
        <span v-if="loading" class="loading-spinner"></span>
        <span v-else>Save Avatar</span>
      </button>
    </div>
    
    <div v-if="error" class="error-message">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { ref, computed, watch, onMounted } from 'vue';
import axios from 'axios';
import bitcatImage from '@/assets/bitcat.webp'
import store from '@/vuex';

export default {
  name: 'AvatarForm',
  props: {
    userTag: {
      type: String,
      required: true
    },
    userSettings: {
      type: Object,
      required: true
    }
  },
  emits: ['updated', 'close'],
  setup(props, { emit }) {
    const avatarUrl = ref(props.userSettings.avatar || '');
    const previewUrl = ref('');
    const loading = ref(false);
    const error = ref('');
    const activeTab = ref('nft');
    const activeNftTab = ref('owned');
    const nftsData = ref({
      owned: { nfts: [], pagination: { total: 0, totalPages: 0 } },
      rented: { nfts: [], pagination: { total: 0, totalPages: 0 } },
      created: { nfts: [], pagination: { total: 0, totalPages: 0 } }
    });
    const currentPage = ref(1);
    const selectedNft = ref(null);
    
    const isValid = computed(() => {
      if (!avatarUrl.value) return true; // Пустая строка - валидно (сбросить аватар)
      
      try {
        new URL(avatarUrl.value);
        return true;
      } catch (e) {
        return false;
      }
    });
    
    watch(avatarUrl, (newValue) => {
      if (newValue && isValid.value) {
        previewUrl.value = newValue;
      } else {
        previewUrl.value = '';
      }
      error.value = '';
    });
    
    const setDefaultAvatar = async () => {
      loading.value = true;
      error.value = '';
      
      try {
        const response = await axios.put(
          `/user-settings/${props.userTag}/avatar`,
          { avatar: null },
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          }
        );
        
        if (response.data && response.data.success) {
          emit('updated', { avatar: null });
          store.commit('setDefaultAvatar');
        } else {
          error.value = 'Failed to reset avatar';
        }
      } catch (err) {
        console.error('Error resetting avatar:', err);
        error.value = err.response?.data?.message || 'Failed to reset avatar';
      } finally {
        loading.value = false;
      }
    };
    
    const saveAvatar = async () => {
      if (activeTab.value === 'url' && !isValid.value) {
        error.value = 'Please enter a valid URL';
        return;
      }
      
      loading.value = true;
      error.value = '';
      
      try {
        let requestData = {};
        
        if (activeTab.value === 'url') {
          requestData = { avatar: avatarUrl.value };
        } else if (activeTab.value === 'nft' && selectedNft.value) {
          requestData = { nftMint: selectedNft.value.assetPublicKey };
        } else {
          error.value = 'Please select an NFT or enter a valid URL';
          loading.value = false;
          return;
        }
        
        const response = await axios.put(
          `/user-settings/${props.userTag}/avatar`,
          requestData,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          }
        );
        
        if (response.data && response.data.success) {
          if (activeTab.value === 'url') {
            emit('updated', { avatar: avatarUrl.value });
          } else {
            store.commit('setAvatar', selectedNft.value.imageUri);
            emit('updated', { avatar: selectedNft.value.imageUri });
          }
        } else {
          error.value = 'Failed to update avatar';
        }
      } catch (err) {
        console.error('Error updating avatar:', err);
        error.value = err.response?.data?.message || 'Failed to update avatar';
      } finally {
        loading.value = false;
      }
    };
    
    const fetchUserNfts = async (tab = 'owned', page = 1) => {
      loading.value = true;
      error.value = '';
      
      try {
        const response = await axios.get(`/nft-bitcat/user/${props.userTag}/nfts`, {
          params: {
            tab,
            page,
            limit: 9 // Показываем по 9 NFT на странице
          }
        });
        
        if (response.data && response.data.success) {
          nftsData.value = response.data;
        } else {
          error.value = 'Failed to load NFTs';
        }
      } catch (err) {
        console.error('Error fetching user NFTs:', err);
        error.value = err.response?.data?.message || 'Failed to load NFTs';
      } finally {
        loading.value = false;
      }
    };
    
    const selectNft = (nft) => {
      selectedNft.value = nft;
      previewUrl.value = nft.imageUri;
    };
    
    const changePage = (page) => {
      currentPage.value = page;
      fetchUserNfts(activeNftTab.value, page);
    };
    
    watch(activeNftTab, (newTab) => {
      currentPage.value = 1;
    });
    
    // При переключении на вкладку NFT, загружаем NFT
    watch(activeTab, (newTab) => {
      if (newTab === 'nft') {
        fetchUserNfts(activeNftTab.value, currentPage.value);
      }
    });
    
    const currentNfts = computed(() => {
      if (!nftsData.value) return [];
      return nftsData.value[activeNftTab.value]?.nfts || [];
    });
    
    const totalPages = computed(() => {
      if (!nftsData.value) return 1;
      return nftsData.value[activeNftTab.value]?.pagination?.totalPages || 1;
    });
    
    const showPagination = computed(() => {
      return totalPages.value > 1;
    });
    
    const hasNfts = computed(() => {
      return nftsData.value.owned.pagination.total > 0 || 
             nftsData.value.rented.pagination.total > 0 || 
             nftsData.value.created.pagination.total > 0;
    });
    
    onMounted(() => {
      fetchUserNfts(activeNftTab.value, currentPage.value);
    });
    
    return {
      avatarUrl,
      previewUrl,
      loading,
      error,
      isValid,
      activeTab,
      activeNftTab,
      nftsData,
      currentPage,
      totalPages,
      selectedNft,
      currentNfts,
      showPagination,
      hasNfts,
      saveAvatar,
      fetchUserNfts,
      selectNft,
      changePage,
      setDefaultAvatar,
      bitcatImage
    };
  }
};
</script>

