<template>
  <div class="password-form">
    <div v-if="hasPassword" class="form-group">
      <label for="current-password" class="form-label">Current Password</label>
      <input 
        type="password" 
        id="current-password" 
        v-model="currentPassword" 
        class="form-input" 
        placeholder="Enter your current password"
      />
    </div>
    
    <div class="form-group">
      <label for="new-password" class="form-label">
        {{ hasPassword ? 'New Password' : 'Password' }}
      </label>
      <input 
        type="password" 
        id="new-password" 
        v-model="newPassword" 
        class="form-input" 
        placeholder="Enter new password"
      />
    </div>
    
    <div class="form-group">
      <label for="confirm-password" class="form-label">Confirm Password</label>
      <input 
        type="password" 
        id="confirm-password" 
        v-model="confirmPassword" 
        class="form-input" 
        placeholder="Confirm new password"
      />
      <div v-if="passwordMismatch" class="validation-error">
        Passwords do not match
      </div>
    </div>
    
    <div class="form-actions">
      <button 
        class="btn-cancel" 
        @click="$emit('close')" 
        :disabled="loading"
      >
        Cancel
      </button>
      <button 
        class="btn-save" 
        @click="savePassword" 
        :disabled="loading || !isValid"
      >
        <span v-if="loading" class="loading-spinner"></span>
        <span v-else>{{ hasPassword ? 'Change Password' : 'Set Password' }}</span>
      </button>
    </div>
    
    <div v-if="error" class="error-message">
      {{ error }}
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue';
import axios from 'axios';

export default {
  name: 'PasswordForm',
  props: {
    userTag: {
      type: String,
      required: true
    },
    hasPassword: {
      type: Boolean,
      default: false
    }
  },
  emits: ['updated', 'close'],
  setup(props, { emit }) {
    const currentPassword = ref('');
    const newPassword = ref('');
    const confirmPassword = ref('');
    const loading = ref(false);
    const error = ref('');
    
    const passwordMismatch = computed(() => {
      return newPassword.value && confirmPassword.value && 
             newPassword.value !== confirmPassword.value;
    });
    
    const isValid = computed(() => {
      if (props.hasPassword && !currentPassword.value) return false;
      if (!newPassword.value || !confirmPassword.value) return false;
      if (passwordMismatch.value) return false;
      return true;
    });
    
    const savePassword = async () => {
      if (!isValid.value) return;
      
      loading.value = true;
      error.value = '';
      
      try {
        let endpoint = '/auth/set-password';
        let data = {
          userTag: props.userTag,
          password: newPassword.value
        };
        
        if (props.hasPassword) {
          endpoint = '/auth/change-password';
          data = {
            userTag: props.userTag,
            currentPassword: currentPassword.value,
            newPassword: newPassword.value
          };
        }
        
        const response = await axios.post(endpoint, data, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        if (response.data && response.data.success) {
          emit('updated', { passwordChanged: true });
        } else {
          error.value = 'Failed to update password';
        }
      } catch (err) {
        console.error('Error updating password:', err);
        error.value = err.response?.data?.message || 'Failed to update password';
        
        if (err.response?.status === 403) {
          error.value = 'Current password is incorrect';
        }
      } finally {
        loading.value = false;
      }
    };
    
    return {
      currentPassword,
      newPassword,
      confirmPassword,
      loading,
      error,
      passwordMismatch,
      isValid,
      savePassword
    };
  }
};
</script>

<style scoped>
.password-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-weight: 500;
  color: #333;
}

.form-input {
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

.form-input:focus {
  outline: none;
  border-color: #ff9800;
  box-shadow: 0 0 0 2px rgba(255, 152, 0, 0.2);
}

.validation-error {
  color: #d32f2f;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem;
}

.btn-cancel,
.btn-save {
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-cancel {
  background-color: #f1f1f1;
  color: #333;
  border: 1px solid #ddd;
}

.btn-save {
  background-color: #ff9800;
  color: white;
  border: none;
}

.btn-cancel:hover {
  background-color: #e5e5e5;
}

.btn-save:hover {
  background-color: #f57c00;
}

.btn-cancel:disabled,
.btn-save:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.error-message {
  color: #d32f2f;
  font-size: 0.875rem;
  margin-top: 0.5rem;
}

.loading-spinner {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
</style>
